.side-drawer{
    position: fixed;
    z-index: 3;
    height:70%;
    background-color: #af727b;
    box-shadow: 2px 0px 5px rgba(0,0,0,0.5);   
    top: 15vh;
    left:0;
    width:70%;
    max-width: 400px;   
    transform: translateX(-100%);
    transition: transform 0.3s ease-out;
}
.side-drawer.open{
    transform: translateX(0);
}
.side-drawer ul{
    height: 50%;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.side-drawer p{
    cursor: pointer;
    color:white;
    text-decoration: none;
    font-size: 1.4rem;
    width: 35%;
}

.side-drawer li{
    margin:0.5rem 0;
    
}

.side-drawer p:hover,
.side-drawer p:active{
    color:rgb(212, 48, 136);
}

