.toolbar{   
    position: fixed;
    z-index: 3;
    top:0;
    left:0;
    width: 100%;
    height: 10vh;    
}
.toolbar-navigation-items{
    display: flex;
    align-items: center;
}

.toolbar-navigation{
    display: flex;
    align-items: center;
    justify-content: space-around;
}
.spacer{
    flex:1;
}

.toolbar-navigation-items ul{
    list-style: none;     
    display: flex;
}
.toolbar-navigation-items li{   
    padding: 0 0.5rem;
}
.toolbar-navigation-items a{
    color:white;
    text-decoration: none;
}

.toolbar-navigation-items a:hover,
.toolbar-navigation-items a:active {
    color:orange;
}
.toolbar-navigation-items{
    display: none;
}