  .navigation{
    display: flex;
    height: 10vh;
    align-items: center;
    flex-direction: column;
    background-color: white;
    justify-content: center;
  }
  .topcontact{
    position: absolute;
    height:3vh;
    display: flex;
    margin-bottom: 2vh;
    width: 100%;
    background-color: #af727b;
    background-image: url("https://www.transparenttextures.com/patterns/climpek.png");
/* This is mostly intended for prototyping; please download the pattern and re-host for production environments. Thank you! */
  }
  .navigation-items{
    background-color: #ffffff;

/* This is mostly intended for prototyping; please download the pattern and re-host for production environments. Thank you! */
    position: absolute;
    top:4vh;
  }
  .navigation ul{
    padding:0;
    list-style: none;     
    display: flex;
    justify-content: center;
    flex-direction: row;
  }
  .navigation a{
    padding: 0 1.5rem;
    color:black;
    font-size: 22px;
    text-decoration: none;
    cursor: pointer;
  }
  .spacer{
    flex:1;
  }
  .topcontact p{ 
    margin-left: 75%;
    margin-right: 4vh;
    font-size: 18px;
    text-align: right;
    vertical-align: middle;
  }
  .topcontact svg{
    padding-left: 0.5%;
    cursor: pointer;
  }
  p{
    margin-top: 0;
    color: white;
  }
  .navigation a:hover,
  .navigation a:active {
    color:rgb(212, 48, 136);
  }
  .admbutton{
    margin-left:4.5vh;
    color:white;
    cursor: pointer;
    position: relative;
    z-index: 4;
  }
  @media only screen and (max-width: 768px) { 
    .navigation{
      height: 50px;
      width: 100%;
      position: relative;
      bottom:100px;
    }
  }