  .home{
    position: relative;
    z-index: 0;
        /* This is mostly intended for prototyping; please download the pattern and re-host for production environments. Thank you! */
    display: flex;
    flex-direction: column;
    align-items: center;
    height:90vh;
    background-color:#f0cece ;
  }
  
  .home p{
    font-family: 'The Girl Next Door', cursive;
    color:#ffffff;
    font-size: 50px;
    padding-top: 8vh;
  }
  .home h1{
    font-family: 'Homemade Apple', cursive;
    color:#ffffff;
    font-size: 100px;
    font-style: italic;
  }
  img {
    width:60vh;
    height: 40vh;
  }
  @media only screen and (max-width: 768px) {
    .home{
      flex-direction: column;
      height:100%;
      width: auto;
    }
    .home h1{
      font-size: 30px;
    }
    .home p{
      font-size: 20px;
    }
    html,body
  {
    width: 100%;
    margin: 0px;
    padding: 0px;
    overflow-x: hidden; 
  }
  }