.aboutMe{
    position: relative;
    z-index: 0;
    height: 140vh;
    width:100%;
    background-color: #ffffff;
    display: flex;
    flex-direction: column;
    justify-content: center;  
  }
  .aboutMe p{
    font-family: 'The Girl Next Door', cursive;
    color:black;
    text-align: center;
  }
  .aboutMe h1{
    font-family: 'The Girl Next Door', cursive;
    text-align: center;
  }
  .info{
    right:10vh;
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
  .infoText{
    width:30%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin-left: 16vh;
  }
  .info img{
    height: 40vh;
    width:30vh;
  }
  .me{
 
    height: 100vh;
    background-color: #ffeaea;
    display: flex;
    flex-direction: column;
    justify-content: center;
    
  }
  .container{
    margin: auto;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-self: center;
  }
  .container-text{
    margin-left: 5vh;
    margin-right: 5vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .me p{

    color:black;
    text-align: center;
  
  }
  .me h1{
    color:black;
    text-align: center;
  }
  .me img {
    height: 30vh;
    width:25vh;
  }
  @media only screen and (max-width: 768px) { 
    .aboutMe{
      flex-direction: column;
      height: 100%;
      justify-content: center;
    }
    .info{
      right:10vh;
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
    .infoText{
      width:30%;
      display: flex;
      justify-content: center;
      flex-direction: row;
    }
    .infoText h1{
      font-size: 30px;
    }
    .infoText p{
      font-size: 15px;
    }
    .me{
 
      height: 100%;
      background-color: #ffeaea;
      display: flex;
      flex-direction: column;
      justify-content: center;
      
    }
    .me h1{
      font-size: 30px;
    }
    .me p{
      font-size: 15px;
    }
    .me img{
      height: 150px;
      width: 150px;
    }
  }